module.exports = "import React from 'react';\nimport PropTypes from 'prop-types';\nimport {withStyles} from '@material-ui/core/styles';\nimport Table from '@material-ui/core/Table';\nimport TableBody from '@material-ui/core/TableBody';\nimport TableCell from '@material-ui/core/TableCell';\nimport TableHead from '@material-ui/core/TableHead';\nimport TableRow from '@material-ui/core/TableRow';\nimport Paper from '@material-ui/core/Paper';\n\nconst CustomTableCell = withStyles(theme => ({\n    head: {\n        backgroundColor: theme.palette.common.black,\n        color          : theme.palette.common.white\n    },\n    body: {\n        fontSize: 14\n    }\n}))(TableCell);\n\nconst styles = theme => ({\n    root : {\n        width    : '100%',\n        marginTop: theme.spacing.unit * 3,\n        overflowX: 'auto'\n    },\n    table: {\n        minWidth: 700\n    },\n    row  : {\n        '&:nth-of-type(odd)': {\n            backgroundColor: theme.palette.background.default\n        }\n    }\n});\n\nlet id = 0;\n\nfunction createData(name, calories, fat, carbs, protein)\n{\n    id += 1;\n    return {\n        id,\n        name,\n        calories,\n        fat,\n        carbs,\n        protein\n    };\n}\n\nconst rows = [\n    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),\n    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),\n    createData('Eclair', 262, 16.0, 24, 6.0),\n    createData('Cupcake', 305, 3.7, 67, 4.3),\n    createData('Gingerbread', 356, 16.0, 49, 3.9)\n];\n\nfunction CustomizedTable(props)\n{\n    const {classes} = props;\n\n    return (\n        <Paper className={classes.root}>\n            <Table className={classes.table}>\n                <TableHead>\n                    <TableRow>\n                        <CustomTableCell>Dessert (100g serving)</CustomTableCell>\n                        <CustomTableCell numeric>Calories</CustomTableCell>\n                        <CustomTableCell numeric>Fat (g)</CustomTableCell>\n                        <CustomTableCell numeric>Carbs (g)</CustomTableCell>\n                        <CustomTableCell numeric>Protein (g)</CustomTableCell>\n                    </TableRow>\n                </TableHead>\n                <TableBody>\n                    {rows.map(row => {\n                        return (\n                            <TableRow className={classes.row} key={row.id}>\n                                <CustomTableCell component=\"th\" scope=\"row\">\n                                    {row.name}\n                                </CustomTableCell>\n                                <CustomTableCell numeric>{row.calories}</CustomTableCell>\n                                <CustomTableCell numeric>{row.fat}</CustomTableCell>\n                                <CustomTableCell numeric>{row.carbs}</CustomTableCell>\n                                <CustomTableCell numeric>{row.protein}</CustomTableCell>\n                            </TableRow>\n                        );\n                    })}\n                </TableBody>\n            </Table>\n        </Paper>\n    );\n}\n\nCustomizedTable.propTypes = {\n    classes: PropTypes.object.isRequired\n};\n\nexport default withStyles(styles)(CustomizedTable);\n"